.account-container {
	margin: 3rem auto;
	display: flex;
	flex-direction: column;
	min-width: 40vw;
	width: fit-content;
}

.account-container .greeting-div {
	font-size: 0.8rem;
	color: var(--dark-gray);
	display: flex;
	flex-direction: row;
	align-items: stretch;
	justify-content: space-between;
	margin-bottom: 2rem;
}

.account-info,
.order-history {
	color: var(--dark-gray);
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	background: whitesmoke;
}
.account-info .header {
	display: flex;
	justify-content: space-between;
	flex-direction: row;
	background: var(--gray);
	color: white;
	padding: 1rem;
}
.account-info .detail {
	display: flex;
	justify-content: space-between;
	padding: 1rem;
	line-height: 1.5rem;
	flex: 1;
}
.order-history .msg {
	text-align: center;
	padding: 2rem;
}
.order-history .history-item {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}
.history-item .qty-desc {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-template-rows: auto;
}
.log-wrapper {
	font-size: 0.8rem;
	color: var(--dark-gray);
	text-transform: capitalize;
}
.log-btn {
	color: white;
	background: var(--dark-gray);
	border: none;
	padding: 0.5rem;
	border-radius: 0.5rem;
	cursor: pointer;
	margin: 0.3rem 0rem;
	font-size: 0.8rem;
}
.log-btn:hover {
	background: var(--green);
}
